<template>
    <div data-view="auth" class="ke-fs-content-view scroll">
        <div class="ke-fs-auth-error">
            <h3>出错啦!</h3>
            <p>{{message}}</p>
            <van-button type="danger" icon="close" @click="_closeWindow" block round>关闭页面</van-button>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                message : ''
            }
        },
        watch : {
            '$route' : function(to) {
                this._getErrorMssage(to.query.code);
            }
        },
        methods : {
            _getErrorMssage(code) {
                switch(code) {
                    case 'AUTH_ERROR':
                        this.message = '无法获取用户信息!';
                        break;
                    default:
                        this.message = '未知错误!';
                }
            },
            _closeWindow() {
                window && window.close();
            }
        },
        created() {
            this._getErrorMssage(this.$router.currentRoute.query.code);
        }
    }
</script>