<template>
    <div data-view="scan-code" class="ke-fs-content-view scroll">
        <template v-if="isInit">
            
            <div class="ke-fs-scan-code-comment" v-if="order">
                <div class="ke-fs-history-item">
                    <h2>订单编号:{{order.orderNum}}</h2>
                    <div class="ke-fs-history-item__info">
                        <!-- <img :src="order.comboImg" /> -->
                        <img :src="order.comboImg && order.comboImg.length > 0 ? `/common/download?fileName=${order.comboImg}` : ''"  />
                        <p class="ke-fs-history-item__info__detail">
                            <strong>{{order.comboName}}</strong>
                            <small>点单时间:{{order.orderDate}}</small>
                        </p>
                    </div>
                </div>
                <div v-if="null != order.eveScore" class="ke-fs-comment">
                    <h3>评分:<strong>{{order.eveScore}}</strong></h3>
                    <p>{{order.eveDesc || '默认好评.'}}</p>
                </div>
                <Comment v-else :orderId="order.id" @success="_onCommentSuccess" @fail="_onCommentFail" />
            </div>
            <div v-else class="ke-fs-not-order">
                <p>您昨天没有预订点餐,无法评价!</p>
                <van-button type="danger" @click="_goIndex" round block>返回首页</van-button>
            </div>
        </template>
    </div>
</template>

<script>
    import { queryTodayOrder } from '@/api/order-api';

    import Comment from '@/components/comment/Comment';

    export default {
        components : {
            Comment
        },
        data() {
            return {
                isInit : false,
                order  : null
            }
        },
        methods : {
            _queryYesterdayOrder() {
                const $loading = this.$toast.loading({
                    message : '加载中...'
                });

                queryTodayOrder(-1)
                    .then(r => {
                        $loading.clear();
                        this.isInit = true;
                        this.order = r.id ? r : null;
                        console.log(JSON.stringify(this.order));
                    })
                    .catch(err => {
                        $loading.clear();
                        this.isInit = true;
                        this.$notify.alert({
                            type : 'danger',
                            message : `获取订单失败,原因:${err}`
                        });
                    })
            },

            _onCommentSuccess() {
                this.$dialog.alert({
                    title : '系统提示',
                    message : '订单评价成功!'
                }).then(this.$router.push('list'));
            },

            _onCommentFail(err) {
                this.$notify.alert({
                    type : 'danger',
                    message : `订单评价失败,原因:${err}`
                });
            },

            _goIndex() {
                this.$router.push('list');
            }
        },
        created() {
            this._queryYesterdayOrder();
        }
    }
</script>