<template>
    <div class="ke-fs-commnet">
        <a class="ke-fs-comment-close">
            <van-icon name="cross" size="20" @click="$emit('close')" />
        </a>
        <van-rate v-model="star" allow-half />
        <van-field v-model="comment" type="textarea" :row="5"  maxlength="200" show-word-limit />
        <van-button type="danger" icon="comment-o" @click="_doComment" block>评价</van-button>
    </div>
</template>

<script>
    import { eveOrder } from '@/api/order-api';

    export default {
        props : {
            orderId : {
                type     : [Number, String], 
                requried : true
            }
        },
        data() {
            return {
                star    : 5,
                comment : ''
            }
        },
        methods : {
            _doComment() {
                const $loading = this.$toast.loading({
                    message : '评分中...',
                    forbidClick : true
                });

                eveOrder(this.orderId, this.star, this.comment)
                    .then(() => {
                        $loading.clear();
                        this.$emit('success', {
                            id      : this.orderId,
                            star    : this.star,
                            comment : this.comment
                        });
                    })
                    .catch(err => {
                        $loading.clear();
                        this.$emit('fail', `订单评价失败,原因:${err}`);
                    });
            }
        }
    }
</script>