import { postJSON } from './http';

function createOrder(comboId) {
    return postJSON('/dc/order/add', { comboId });
}

function modifyOrder(orderId, comboId) {
    return postJSON('/dc/order/mod', { orderId, comboId });
}

function removeOrder(id) {
    return postJSON('/dc/order/del', { id });
}

function queryTodayOrder(day = 0) {
    return postJSON('/dc/order/getTodayOrder', { day });
}

function queryOrderList(pageNo, pageSize) {
    return postJSON('/dc/order/list', { pageNo, pageSize });
}

function eveOrder(orderId, eveScore, eveDesc) {
    return postJSON('/dc/order/eveOrder', { orderId, eveScore, eveDesc });
}

export {
    eveOrder,
    createOrder,
    modifyOrder,
    removeOrder,
    queryOrderList,
    queryTodayOrder
}