<template>
    <div data-view="order-history" class="ke-fs-content-view scroll">
        <van-pull-refresh v-if="orders.length > 0" v-model="isRefresh" @refresh="_queryHistoryOrderList(true)">
            <van-list v-model="isLoading" finished-text="已全部加载完毕!" :finished="isFinished">
                <div v-for="order in orders" :key="order.id" class="ke-fs-history-item">
                    <h2>订单编号:{{order.orderNum}}</h2>
                    <div class="ke-fs-history-item__info">
                        <!-- <img :src="order.comboImg" /> -->
                        <img :src="order.comboImg && order.comboImg.length > 0 ? `/common/download?fileName=${order.comboImg}` : ''"  />
                        <p class="ke-fs-history-item__info__comment">
                            <template v-if="null != order.eveScore">
                                 <strong>{{order.eveScore}}<small>分</small></strong>
                            </template>
                            <van-button v-else type="danger" @click="_showComment(order.id)" size="small">立即评价</van-button>
                        </p>
                        <p class="ke-fs-history-item__info__detail">
                            <strong>{{order.comboName}}</strong>
                            <small>点单时间:{{order.orderDate}}</small>
                        </p>
                    </div>
                </div>
            </van-list>
        </van-pull-refresh>
        <van-empty v-else description="暂无历史点餐订单数据." />
        <van-overlay :show="null != commentOrderId">
            <Comment 
                :orderId="commentOrderId" 
                @close="commentOrderId = null" 
                @success="_commentOrderSuccess" 
                @fail="_commentOrderFail" />
        </van-overlay>
    </div>
</template>

<script>
    import Comment from '../../components/comment/Comment';
    import { queryOrderList } from '@/api/order-api';

    export default {
        components : {
            Comment
        },
        data() {
            return {
                isRefresh  : false,
                isLoading  : false,
                isFinished : true,
                pageNo     : 1,
                pageSize   : 10,
                orders     : [1],
                img        : require('../../assets/img/kfc.jpg'),
                commentOrderId : null
            }
        },
        methods : {
            _onHistoryRefresh() {

            },
            _showComment(id) {
                console.log(id);
                this.commentOrderId = id;
            },

            _commentOrderSuccess(data) {
                console.log(JSON.stringify(data));
                this.commentOrderId = null;
                this._showNotify('success', '评价成功!');
                const index = this.orders.findIndex(o => data.id == o.id), _order = this.orders[index];
                if(_order) {
                    _order.eveScore = data.star;
                    _order.eveDesc  = data.comment;
                }
            },

            _commentOrderFail(message) {
                this._showNotify('danger', message);
            },

            _showNotify(type, message) {
                this.$notify.clear();
                this.$notify({ type, message });
            },

            _queryHistoryOrderList(isNewQuery = false) {
                this.pageNo = isNewQuery ? 1 : this.pageNo + 1;
                this.isLoading = true;
                queryOrderList(this.pageNo, this.pageSize)
                    .then(r => {
                        this.isRefresh = false;
                        this.isLoading = false;
                        this.isFinished = r.total - this.pageNo * this.pageSize <= 0;
                        this.orders = r.rows || [];
                    })
                    .catch(err => {
                        this.isLoading = false;
                        this.isFinished = false;
                        this.$isRefresh = false;
                        this.$notify.alert({
                            type : 'danger',
                            message : `获取历史订单数据失败,原因:${err}`
                        });
                    })
            }
        },
        created() {
            this._queryHistoryOrderList(true);
        }    
    }
</script>