<template>
    <div data-view="my-order" class="ke-fs-content-view scroll">
        <template v-if="isInit">
            <template v-if="order">
                <h2>
                    <van-icon name="balance-list-o" size="24" />
                    <small>订单编号</small>
                    <span>{{0 == order.status ? order.orderNum : '等待订单确认!'}}</span>
                </h2> 
                <van-cell-group title="订单详情">
                    <van-cell title="商品详情">
                        <div class="ke-fs-order-info">
                            <h4>
                                <span>{{order.comboName}}</span>
                                <small>1份</small>
                            </h4>
                        </div>
                        <template #label>
                            <!-- <img :src="order.comboImg" class="ke-fs-order-pic"/> -->
                            <img :src="order.comboImg && order.comboImg.length > 0 ? `/common/download?fileName=${order.comboImg}` : ''" class="ke-fs-order-pic" />
                        </template>
                    </van-cell>
                    <van-cell title="订单状态">
                        <van-tag v-if="0 == order.status" type="success" round>已确认</van-tag>
                        <van-tag v-else type="danger" round>待确认</van-tag>
                    </van-cell>
                    <van-cell title="下单时间" :value="order.orderDate" />
                </van-cell-group>
                <template v-if="0 != order.status">
                    <van-button type="danger" icon="failure" @click="_cancelOrder(order.id)" plain round block>修改订单</van-button>
                    <van-button type="danger" icon="close" @click="_closeOrder(order.id)" round block>取消订单</van-button>
                </template>
                
            </template>
            <template v-else>
                <van-empty description="您还未产生点餐订单." />
                <van-button  type="danger" icon="shopping-cart-o" @click="_gotoFoodList" class="no-mgr" round block>立即点单</van-button>
            </template>
        </template>
    </div>
</template>

<script>
    import { queryTodayOrder, removeOrder } from '@/api/order-api';
    import { Dialog, Notify } from 'vant';

    export default {
        data() {
            return {
                order  : null,
                img    : require('../../assets/img/kfc.jpg'),
                isInit : false
            }
        },
        methods : {
            _cancelOrder(id) {
                Dialog.confirm({
                    title   : '系统提示',
                    message : '是否立即修改订单?'
                })
                .then(() => this.$router.push(`/list?isMod=true&oid=${id}`))
                .catch(()=>{});
                
            },

            _closeOrder(orderId) {
                Dialog.confirm({
                    title   : '系统提示',
                    message : '是否立即取消订单?' 
                })
                .then(() => this._doCanncelOrder(orderId))
                .catch(() => {});
            },

            _doCanncelOrder(id) {
                removeOrder(id)
                    .then(()=> {
                        Notify({
                            type : 'success',
                            message : `订单取消成功!`
                        });
                        this.order = null;
                    })
                    .catch(err => Notify({
                        type : 'danger',
                        message : `取消订单失败,原因:${err}`
                    }));
            },

            _gotoFoodList() {
                this.$router.push('/list');
            },

            _queryTodayOrder() {
                const $loading = this.$toast.loading({
                    message : '加载中...'
                });
                queryTodayOrder()
                    .then(order => {
                        this.order = order.id ? order : null;
                        this.isInit = true;
                        console.log(JSON.stringify(this.order));
                        $loading.clear();
                    })
                    .catch(err => {
                        this.isInit = true;
                        $loading.clear();
                        Dialog.alert({
                            title   : '系统提示',
                            message : `获取用户订单失败,原因:${err}`,
                            theme   : 'round-button'
                        });
                    });
            }
        },

        created() {
            this._queryTodayOrder();
        }
    }
</script>