import Vue from 'vue';
import VueRouter from 'vue-router';

import Auth from '../views/auth/Auth';
import FoodList from '../views/food-list/FoodList';
import MyOrder from '../views/my-order/MyOrder';
import OrderHistory from '../views/order-history/OrderHistory';
import ScanCodeComment from '../views/scan-code/ScanCodeComment';
import Error from '../views/error/Error';

Vue.use(VueRouter);



const routes = [{
    path        : '/auth',
    component   : Auth
},{
    path      : '/list',
    component : FoodList
}, {
    path      : '/myOrder',
    component : MyOrder
},{
    path      : '/history',
    component : OrderHistory
},{
    path      : '/scanCode',
    component : ScanCodeComment,
},{
    path      : '/error',
    component : Error
}, {
    path      : '/',
    redirect  : '/list'
}];

const router = new VueRouter({
  routes
});
router.beforeEach((to, form, next)=> {
    const path = to.path;
    if('/auth' == path || '/error' == path) {
        next();
    } else {
        const sid = window.localStorage ? window.localStorage.getItem('sid') : window.___fsAppUID___;
        sid  ? next() : next('/error?code=AUTH_ERROR');
    }
});

export default router;
