import { postJSON } from './http';


function queryComboList() {
    return new Promise((resolve, reject) => {
        Promise.all([
            postJSON('/dc/combo/list'), 
            postJSON('/dc/combo/getComboCount'), 
            postJSON('/dc/order/getTodayOrder', {day : 0})
        ])
        .then(([ list = [], cnts = [], order = {} ]) => {
            const cntObjs = cnts.reduce((obj, cntInf) => {
                obj[`id_${cntInf.id}`] = cntInf.cnt || 0;
                return obj;
            }, {});
            list.forEach(v => v.cnt = cntObjs[`id_${v.id}`] || 0);
            resolve({
                list,
                status : order.status ? order.status : -1
            });
        })
        .catch(err => reject(err));
    });
}

export {
    queryComboList
}