<template>
    <router-view />
</template>

<script>
    export default {
        data() {
            return {
                hasError : false,
                message  : null
            }
        },
        watch : {
            '$route' : function() {
                this._checkRouteParams();
            } 
        },
        methods : {
            _checkRouteParams() {
                const params = this.$router.currentRoute.query,
                      sid    = params.sid || '',
                      action = params.action || 'list';
                if(sid.trim().length > 0) {
                    if(window.localStorage) {
                        window.localStorage.setItem('sid', sid.trim());
                    } else {
                        window.___fsAppUID___  = sid.trim();
                    }
                    'scan' === action ? this.$router.push('/scanCode') : this.$router.push('/list');
                } else {
                    this.$router.push('/error?code=AUTH_ERROR');
                }
                
            }
        },
        created() {
            this._checkRouteParams();
        }
    }
</script>